import React, {Component} from 'react';
import {
    Card,
    Button,
    Modal,
    Form,
    Input,
    message,
    Table
} from 'antd'
import {reqAddRole,reqRoles,reqUpdateRole} from "../../api"
import UpdateRole from './UpdateRole'
import storageUtils from '../../utils/storageUtils'
import {PAGE_NUMBER} from '../../utils/constants'
export default class Role extends Component {

    constructor(props){
        super(props)
        this.menus = React.createRef()
    }
    state = {
        showAdd:false,  // 
        roles:[],    //
        selectedRowKeys:[],
        showUpdate:false,  // 
        role:{},//
        loading:true,
    }

    componentDidMount(){
        this.getRoles()
    }
    //Table
    columns = [
        {
            title: '',
            dataIndex: 'name',
        },
        {
            title: '',
            dataIndex: 'create_time',
            render:(create_time)=>{
                // console.log(create_time,typeof create_time);
                return new Date(create_time).toLocaleString()
            }
        },
        {
            title: '',
            dataIndex: 'auth_time',
            render:(auth_time)=>{       //render
                // console.log(auth_time)
                if(auth_time){
                    return new Date(auth_time).toLocaleString()
                }
            }
        },
        {
            title: '',
            dataIndex: 'auth_name',
        }
    ];

    getRoles = async()=>{
        this.setState({loading:true})
        const result = await reqRoles()
        this.setState({loading:false})

        // console.log(result)
        const {status,msg,data} = result
        if(status===0){ //
            this.setState({roles:data})
        }
    }


    //ok
    addRole = ()=>{
        this.AddRoleForm = this.form.props.form
        this.AddRoleForm.validateFields(async (err,values)=>{
           if(!err){   //   
               const result = await reqAddRole(values) ;// parentId:0  categoryName:
               const {status,msg} = result
               console.log(result)
               if(status===0){ //    
                   message.success(msg)
                   this.AddRoleForm.resetFields()
                   this.setState({showAdd: false});
                   this.getRoles()
               }else{
                   message.error(msg)
               }
           }
       })

    }

    //ok
    updateRole = async ()=>{
        // console.log('',this.menus.current.state.selectedKeys)
        const {role} = this.state
        //
        //   
        const user = storageUtils.getUser()
        //
        role.auth_time = new Date()
        role.menus = this.menus.current.state.checkedKeys
        role.auth_name = user.username
        const result = await reqUpdateRole(role)

        const {status,msg} = result
        if(status===0){ //
            /*
            *   
            *      1.   
            * */
           if(user.role_id===role._id){
                message.success('')
                storageUtils.removeUser()
                this.props.history.replace('/login')
           }else{
               message.success(msg)
               this.setState({showUpdate: false});
               this.getRoles()

           }
        }else{
            message.error(msg)
        }
    }

    handleCancel = e => {
        //
        this.setState({
            showAdd: false,
            showUpdate:false
        });
    }

    //
    onRow = role => ({
        //  role
        onClick: () => this.setState({
            selectedRowKeys:[role._id],
            role
        })
    })

    render() {
        const {roles,role,loading} = this.state
        const title = (
            <span>
               <Button type='primary' icon={'plus'} onClick={()=>this.setState({showAdd:true})}></Button>
                <Button
                    type='primary'
                    style={{margin:10}}
                    disabled={!role._id}
                    onClick={()=>this.setState({showUpdate:true})}
                ></Button>
            </span>
        )
        return (
            <Card title={title}>
                <Table
                    dataSource={roles}  //
                    columns={this.columns}       //
                    rowKey='_id'        //
                    bordered
                    loading={loading}
                    rowSelection={{
                        type:'radio',
                        selectedRowKeys:this.state.selectedRowKeys,
                        // onChange:(selectedRowKeys)=>{   //
                        //   selectedRowKeys  _id
                        //     this.setState({selectedRowKeys})
                        // }
                    }}
                    pagination={{defaultPageSize:PAGE_NUMBER}}    //
                    onRow={this.onRow}
                />
                <Modal
                    title=""
                    visible={this.state.showAdd}
                    onOk={this.addRole}
                    onCancel={this.handleCancel}
                >
                    <AddRole wrappedComponentRef={(form) => this.form = form}/>
                </Modal>
                <Modal
                    title=""
                    visible={this.state.showUpdate}
                    onOk={this.updateRole}
                    onCancel={this.handleCancel}
                >
                    <UpdateRole ref={this.menus} role={role}/>
                </Modal>
            </Card>
        );
    }
}

class AddRole extends Component {
    render(){
        const {getFieldDecorator} = this.props.form
        return (
            <Form>
                <Form.Item>
                    {
                        getFieldDecorator('roleName', {
                            rules: [
                                {required: true, message: ''}
                            ]
                        })(
                            <Input placeholder=''/>
                        )
                    }

                </Form.Item>
            </Form>
        )
    }
}

AddRole = Form.create()(AddRole);

