import React, { Component } from 'react';
import { Card, Button, Icon, Modal, message, Table } from 'antd';
import AddCategory from './AddCategory'
import { reqAddCategory, reqCategorys,reqUpdataCategorys } from '../../api'
import Mybutton from '../../components/my-button/my-button';
import UpdateCategory from'./updateCategory';


export default class Category extends Component {
    //
    state = {
        visible: false, // 
        showUpdate:false,//  
        categorys: [],//
        loading: true, //
        parentId:'0', //tabel
        subCategorys:[], //
        parentName:'', //
    }


    //
    componentDidMount() {
        this.getCategorys()
    }

    //
    UNSAFE_componentWillMount() {
        this.initCoumns()
    }

    //
    initCoumns = () => {
        this.columns = [
            {
                title: '',
                dataIndex: 'name',
            },
            {
                title: '',
                width: 200,
                render: (category) => {
                    return (
                        <span>
                            <Mybutton onClick={()=>this.updataCategory(category)}></Mybutton>
                            {
                              this.state.parentId==='0'? <Mybutton onClick={()=>this.showSubCategory(category)}></Mybutton>:null
                            }
                        </span>
                    )
                }
            },
        ];
    }

    //
    showAdd = () => {
        this.setState({ visible: true })
    } 

    //
    updataCategory = (category) => {
        this.category = category
         this.setState({showUpdate:true})
    }


    //
    getCategorys = async (parentId) => {
        //
        this.setState({ loading: true })
        //const {parentId} = this.state
        parentId = parentId||this.state.parentId        
        const result = await reqCategorys(parentId)
        //
        this.setState({ loading: false })
        const { msg, data, status } = result
        if (status === 0) { //
            // message.success(msg)
            if(parentId==='0'){ //
              this.setState({categorys:data})
            }else{  //
                this.setState({ subCategorys: data })
            }
           
        } else {
            message.error(msg)
        }
    }


    //  
    onOk = () => {
        this.form.validateFields(async (err, values) => {
            if (!err) {  //  
                const result = await reqAddCategory(values); //parentId:'0',categoryName
                const { msg, data, status } = result
                if (status === 0) {  //  
                    message.success(msg)
                    // 
                    this.form.resetFields()
                    this.setState({ visible: false })
                    //
                    if(values.parentId===this.state.parentId){  //parentIdparentId
                        this.getCategorys()
                    }else if(values.parentId==='0'){  //parentId0 0
                        this.getCategorys('0')
                    }
                    
                } else {
                    message.error(msg)
                    this.setState({ visible: true })
                }
            }
        })
    }

    //
    onCancel = () => {
        //
        this.form.resetFields()
        this.setState({ 
            visible: false,
            showUpdate:false
        })
    }


    //
    showSubCategory=(category)=>{
       this.setState({   //parentId id setSate
           parentId:category._id,
           parentName:category.name
       },()=>{  //parentId 
        this.getCategorys();
       })
      
    }

    //
    showCategorys=()=>{
        this.setState({  //parentIdparentName
            parentId:'0',
            parentName:'',
            subCategorys:[] //
        })
    }

    //
    updataOkay=()=>{
        this.form.validateFields(async (err, values) => {
              if(!err){
                    values._id = this.category._id
              const result = await reqUpdataCategorys(values);
              const { msg, data, status } = result
              if(status===0){
                  message.success(msg)
                  this.form.resetFields()
                  this.setState({ showUpdate: false })
                  this.getCategorys()
              }else{
                  message.error(msg)
              }
              }
        })
    }


    render() {
        const { categorys, loading,subCategorys,parentId,parentName } = this.state
        let CategoryName;
        if(this.category){
            CategoryName = this.category.name
        }else{
            CategoryName = '';
        }
        const title = parentId==='0'?'':(
            <span>
                 <Mybutton onClick={this.showCategorys}></Mybutton>
                <Icon type='arrow-right'/>
                {parentName}
            </span>
        )
        const extra = (
            <Button type='primary' onClick={this.showAdd}>
                <Icon type='plus' />
                <span></span>
            </Button>
        )


        return (
            <Card title={title} extra={extra}>
                <Table
                    dataSource={parentId==='0'?categorys:subCategorys}  //
                    columns={this.columns}     //
                    loading={loading}   //
                    rowKey={"_id"}   //id
                    pagination={{ defaultPagesSize: 10 }}  //
                    bordered   //
                />
                <Modal
                    title=""
                    visible={this.state.visible}
                    onOk={this.onOk}
                    onCancel={this.onCancel}
                >
                    <AddCategory
                        categorys={categorys}
                        parentId={parentId}
                        setForm={(form) => this.form = form} />
                </Modal>

                <Modal
                    title=""
                    visible={this.state.showUpdate}
                    onOk={this.updataOkay}
                    onCancel={this.onCancel}
                >
                    <UpdateCategory
                    CategoryName={CategoryName}
                     setForm={(form) => this.form = form}
                    />
                </Modal>
            </Card>
        );
    }
}