import React, {Component} from 'react';
import ReactEcharts from 'echarts-for-react';
import {Card, Button} from"antd"
import echarts from 'echarts'


export default class line extends Component {

  state={
      books:[1000,2000,2323,5454,4233,6533,2123],//
      sales:[2313,3423,2313,453,3424,4532,3132],//
  }

    getOption1=()=>{
        let options = {
            title:{
            text:'',
            textStyle:{
                color:'pink'
            }
            },
            legend:{ //
                data:[''],
                icon:'triangle'
            },
            xAxis:{ 
                data:['','','','','','','']
            },
            tooltip: { //
              trigger:'item'
            },
            yAxis:{},
            series:[
                {
                    name:'',
                    type:'line',
                    data:[1000,2000,1500,3000,2000,1200,800]
                }
            ]
        }
        return options
    }
     
    getOption2=(books,sales)=>{
        let options = {
            title:{
            text:'',
            textStyle:{
                color:'pink'
            }
            },
            legend:{ //
                data:['',''],
                icon:'circle'
            },
            xAxis:{ 
                data:['','','','','','','']
            },
            tooltip: { //
              trigger:'item'
            },
            yAxis:{},
            series:[  //
                {
                    name:'',
                    type:'line',
                    data:books
                },
                {
                    name:'',
                    type:'line',
                    data:sales
                }
            ]
        }
        return options
    }

    updataStore=()=>{
        this.setState(state=>({
           books:state.books.map(item=>item+100),
           sales:state.sales.map(item=>item-20)
        }))
    }



    render() {
        const title = <Button type={'primary'}onClick={this.updataStore}></Button>
        const {books,sales} = this.state
        return (
           <div>
           <Card >
               <ReactEcharts option={this.getOption1()} />
            </Card>
            <Card title={title}>
               <ReactEcharts option={this.getOption2(books,sales)} />
            </Card>
           </div>
        );
    }
}