import React, {Component} from 'react';
import {
    Card,
    Button,
    Modal,
    message,
    Table
} from 'antd'
import AddUser from './AddUser'
import {reqRoles, reqAddUser, reqUsers,reqUpdateUser,reqDeleteUser} from "../../api"
import Mybutton from "../../components/my-button/my-button"
import {PAGE_NUMBER} from '../../utils/constants'
const { confirm } = Modal;
export default class User extends Component {

    state = {
        visible:false, // 
        roles:[],
        users:[],
        roleName:{}   //{id:}

    }
    componentDidMount(){
        this.getData()
    }

    getData = async()=>{
        const result = await Promise.all([reqUsers(),reqRoles()])
        // console.log(result)
        // const {status,msg,data} = result
        // if(status===0){ //
        //     this.setState({users:data})
        // }
        this.setState({
            users:result[0].data,
            roles:result[1].data
        })

        this.initRoles()

    }
    initRoles = ()=>{
        // console.log(this.state.roles)
        const roleName = this.state.roles.reduce((pre,next)=>{
            pre[next._id] = next.name
            return pre
        },{})
        this.setState({roleName})
    }
    // getUsers = async()=>{
    //     const result = await reqUsers()
    //     // console.log(result)
    //     const {status,msg,data} = result
    //     if(status===0){ //
    //         this.setState({users:data})
    //     }
    // }
    //
    // //
    // getRoles = async()=>{
    //     const result = await reqRoles()
    //
    //     // console.log(result)
    //     const {status,msg,data} = result
    //     if(status===0){ //
    //         this.setState({roles:data})
    //     }
    // }
    handleCancel = e => {
        //
        this.setState({
            visible:false
        });
    }

    //
    showUpdate = (user)=>{
        this.user = user
        this.setState({visible:true})
    }

    showAdd = ()=>{
        this.user = null
        this.setState({visible:true})
    }

    showDelete = (user)=>{
        confirm({
            title: `${user.username}?`,
            okText: '',
            okType: 'danger',
            cancelText: '',
            onOk:async ()=>{  //
                const result = await reqDeleteUser(user)
                const {status,msg} = result
                if(status===0){
                    message.success(msg)
                    this.getData()
                }
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    columns = [
        {
            title: '',
            dataIndex: 'username',
        },
        {
            title: '',
            dataIndex: 'create_time',
            render:(create_time)=>new Date(create_time).toLocaleString()
        },
        {
            title: '',
            dataIndex: 'phone',
        },
        {
            title: '',
            dataIndex: 'role_id',   //id  roleName = {id:name}

            render:(role_id)=>{
                console.log(this.roleName)
                return this.state.roleName[role_id]

                // return this.state.roles.find(item=>item._id===role_id).name
            }
        },
        {
            title: '',
            width:300,
            render:(user)=>{
                // console.log(a);//render 
                return (
                    <span>
                        <Mybutton onClick={()=>this.showUpdate(user)}></Mybutton>
                        <Mybutton onClick={()=>this.showDelete(user)}></Mybutton>
                    </span>
                )
            }
        }
    ]
    ///ok
    addOrUpdateUser = ()=>{
        // console.log('addUser')
        this.form.props.form.validateFields(async (err,values)=>{
            console.log(err, values)

            if(!err){   //   

                let result = null;
                if(this.user){  //
                    values._id = this.user._id
                    result = await reqUpdateUser(values) ;
                }else{  //
                    result = await reqAddUser(values) ;
                }
                const {status,msg} = result
                console.log(result)
                if(status===0){ //    
                    message.success(msg)
                    this.form.props.form.resetFields()
                    this.setState({visible: false});
                    this.getData()
                }else{
                    message.error(msg)
                }
            }
        })
    }


    render() {
        const {roles,users} = this.state
        // console.log(this.user)
        const {user} = this
        const title = (
            <Button
                type='primary'
                icon={'plus'}
                onClick={this.showAdd}></Button>
        )
        return (
            <Card title={title}>
                <Table
                    dataSource={users}  //
                    columns={this.columns}       //
                    rowKey='_id'        //
                    // loading={loading}      //
                    bordered
                    pagination={{defaultPageSize:PAGE_NUMBER}}    //
                />
                <Modal
                    title={user?"":""}
                    visible={this.state.visible}
                    onOk={this.addOrUpdateUser}
                    onCancel={this.handleCancel}
                >
                    <AddUser
                        wrappedComponentRef={(form) => this.form = form}
                        user={user}
                        roles={roles}/>
                </Modal>
            </Card>
        );
    }
}

