import React, { Component } from 'react'
import { Upload, Icon, Modal, message } from 'antd';
import {reqDeleteimg} from '../../api/index'
import {BASE_URL}from '../../utils/constants'

export default class PictureWall extends Component {
 constructor(props){
   super(props)

   const {imgs} = this.props   // undefined  imgs
   console.log(imgs)
   let fileList=[]
   if(imgs){   //
    fileList = imgs.map((item,index)=>({
             uid:-index,
             name:item,
             status:'done',
             url:BASE_URL + item 
       }))
   }
   this.state = {
    previewVisible: false,  //
    previewImage: '',  //
    fileList //
  }
 }

    //filelist 
    getImgs=()=>this.state.fileList.map(item=>item.name)

      //   filestatusdone uploading
    handleChange = async({file, fileList }) =>{ 
        //filefileList
        //fileList
     
        //url name
          if(file.status==='done'){   //
              
                file = fileList[fileList.length-1]
                const {status,data,msg} = file.response
                message.success(msg)

                file.name = data.name
                file.url = data.url
          }else if(file.status==='removed'){  //
               const result = await reqDeleteimg(file.name)
               const {msg} = result
               message.success(msg)
          }
          this.setState({ 
            fileList 
        });
        }

    //
    handleCancel = () => this.setState({ previewVisible: false });


    //
    handlePreview = async file => {

        // url name
      this.setState({
        previewImage: file.url || file.preview,
        previewVisible: true,
      });
    };



    render() {
        const { previewVisible, previewImage, fileList } = this.state;
        console.log(this.props.imgs)
        const uploadButton = (
          <div>
            <Icon type="plus" />
            <div className="ant-upload-text">Upload</div>
          </div>
        );
        return (
          <div className="clearfix">
            <Upload
              action="/img/uploads"
              listType="picture-card"
              fileList={fileList}
              name='image'
              accept='image/*'  //  png jpg  jpeg webp  image/*
             onPreview={this.handlePreview}
              onChange={this.handleChange}
            >
              {fileList.length >= 8 ? null : uploadButton} 
            </Upload>
            <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
              <img alt="example" style={{ width: '100%' }} src={previewImage} />
            </Modal>
          </div>
        );
      }
    }
    
