/*
  axios  'get'post'
*/
import axios from 'axios'

//axios
axios.interceptors.response.use(res=>res.data)


//axios
export default function ajax(url, data = {}, method = 'get') {
    /*
      url
      data 
      method  get"    
    */
    return new Promise(resolve => {
        let p = null
        if (method === 'get') {
         p = axios.get(url, {
                params: data
            })
        } else {
            p = axios.post(url, data)
        }
        p.then(res => {
          resolve(res)
        })
    })
}

